.marcoScroll{
    height: 50px;
    width: 50px;
    position: fixed;
    bottom: 50px;
    right:calc(0 - 50px);
    cursor: pointer;
    -webkit-transition: 0.3s;
    -o-transition: 0.3s;
    transition: 0.3s;
    padding: 5px;
}
.scroll{
    height: 100%;
    width: 100%;
    background: url('../assets/icons/subir.svg') no-repeat center;
    background-size: 50px;
    -webkit-transition: 1s;
    -o-transition: 1s;
    transition: 1s;
    -webkit-transform: rotateX(0deg);
            transform: rotateX(0deg);
}

.marcoScroll:hover > .scroll{
    background: url('../assets/icons/subirPink.svg') no-repeat center;
    background-size: 50px;
    -webkit-transform: rotateX(380deg);
            transform: rotateX(380deg);
    color: #e88697;
}


